.addres {
    a {
        position: relative;
        padding-left: 2.3rem;
        font-style: normal;
        font-weight: normal;
        font-size: 1.4rem;
        line-height: 1.9rem;
        font-feature-settings: "case" on, "cpsp" on;
        color: #828282;
        &::after {
            position: absolute;
            content: "";
            background: url("../img/icons/location.svg");
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center center;
            width: 1.59rem;
            height: 2.1rem;
            top: 0.1rem;
            left: 0.1rem;
        }
    }
}
