.guarantees {
    padding: 2.5rem 0 2.8rem 0;

    @media (min-width: 768px) {
        padding: 4.8rem 0 3.1rem 0;
    }

    @media (min-width: 1320px) {
        padding: 4.6rem 0 4.5rem 0;
    }
}

.section__subtitle--guarantees {

    @media (min-width: 768px) {
        font-size: 1.8rem;
        line-height: 2.8rem;
    }

    @media (min-width: 1320px) {
        font-size: 2.4rem;
        line-height: 3.7rem;
    }
}

.content-block--guarantees {
    margin-bottom: 3.5rem;

    @media (min-width: 768px) {
        margin-bottom: 4.5rem;
    }
    
    @media (min-width: 1320px) {
        margin-bottom: 6.4rem;
        width: 100%;
        padding-right: 4rem;
    }
}

.content-block__img--guarantees {
    height: 17rem !important;

    @media (min-width: 414px) {
        height: 19.9rem !important;
    }

    @media (min-width: 768px) {
        height: 37.5rem !important;
    }

    @media (min-width: 1320px) {
        height: 32.6rem !important;
        margin-right: 0 !important;
    }
}

.content-block__text--guarantees {

    @media (min-width: 1320px) {
        max-width: 53rem;
    }
}