/*   */
#menu__toggle {
    opacity: 0;
    @media (min-width: 1320px) {
        display: none;
    }
}
input#menu__toggle[type="checkbox"] + label {
    &::before {
        background: transparent;
    }
    &::after {
        width: 0;
        height: 0;
        border: none;
        border-width: 0;
        transform: rotate(0deg);
    }
}

/*   */
.menu__btn {
    display: flex; /*  flex    */
    align-items: center; /*    */
    position: absolute;
    // top: 0.5rem;
    top: 200%;
    right: 10%;
    width: 3rem;
    height: 2.6rem;
    cursor: pointer;
    z-index: 91;
    @media (min-width: 900px) {
        right: 15%;
    }
    @media (min-width: 1320px) {
        display: none;
    }
}
/*  "" */
.menu__btn > span,
.menu__btn > span::before,
.menu__btn > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 0.2rem;
    background-color: #616161;
    transition-duration: 0.25s;
}
.menu__btn > span::before {
    content: "";
    top: -8px;
}
.menu__btn > span::after {
    content: "";
    top: 8px;
}
/*   */
.menu__box {
    display: block;
    position: fixed;
    visibility: hidden;
    overflow-x: auto;
    height: 100%;
    width: 100%;
    max-width: 41.1rem;
    top: 0;
    right: -100%;
    // transition-duration: 1s;
    background: rgb(255, 255, 255);
    // background: linear-gradient(to left, black 50%, blue 50%);
    padding: 8rem 2rem;
    transition: all 0.4s;
    z-index: 90;
    .social ul {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
    }
    @media (min-width: 1320px) {
        display: block;
        width: 100%;
        background: transparent;
        position: inherit;
        visibility: visible;
        overflow: initial;
        height: 100%;
        width: 100%;
        max-width: 100rem;
        top: 0;
        right: 0;
        padding: 0;
    }
}
.menu__box-bottom {
    @media (min-width: 1320px) {
        display: none;
    }
}
#menu__toggle:checked ~ .menu__btn > span {
    transform: rotate(45deg);
}
#menu__toggle:checked ~ .menu__btn > span::before {
    top: 0;
    transform: rotate(0);
}
#menu__toggle:checked ~ .menu__btn > span::after {
    top: 0;
    transform: rotate(90deg);
}
#menu__toggle:checked ~ .menu__box {
    visibility: visible;
    right: 0;
    // transition-duration: 0.7s;
    transition: all 0.4s;
    z-index: 90;
    filter: drop-shadow(0px 0px 7px rgba(0, 0, 0, 0.4));
}
