.reviews {
    padding: 3rem 0 4.2rem 0;

    @media (min-width: 768px) {
        padding: 3.8rem 0 6.2rem 0;
    }

    @media (min-width: 1320px) {
        padding: 5rem 0 8.7rem 0;
    }

    .btn--green {
        min-width: 11.5rem;
    }
}

.section__title--reviews {
    margin: 1.7rem 0 3.4rem 0;
}

.swiper-reviews {
    width: 30rem;
    min-height: 63.7rem;
    // height: 50.4rem;

    @media (min-width: 414px) {
        width: 35.4rem;
        // height: 50.4rem;
    }

    @media (min-width: 768px) {
        width: 66.5rem;
        min-height: initial;
    }

    @media (min-width: 1320px) {
        width: 100rem;
        // height: 22.8rem;
    }
}

.slide-reviews {
    width: 30rem;
    height: auto;
    display: flex;
    flex-direction: column;
    padding: 3rem 2.2rem 3.6rem 3rem;
    background: $white;
    border: .1rem solid $gray3;
    border-radius: 1rem;

    @media (min-width: 414px) {
        width: 35.4rem;
        padding: 3rem 2.2rem 3.6rem 2.2rem;
    }

    @media (min-width: 768px) {
        width: 66.5rem;
        // height: 50.4rem;
    }

    @media (min-width: 1320px) {
        width: 100rem;
        // height: 22.8rem;
        // display: flex;
        padding: 3rem 3.7rem 2.8rem 3.5rem;
    }
}

.slide-reviews__inner {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: flex-end;

    @media (min-width: 1320px) {
        flex-direction: row;
        align-items: initial;
    }

    .slide-reviews__text {
        max-width: 77.7rem;
    }

    .slide-reviews__author {

        .author__avatar {
            width: 10rem;
            height: 10.5rem;
            overflow: hidden;
            border-radius: 50%;

            img {
                width: 10rem;
                height: 10.5rem;
            }
        }

        .author__name {
            text-align: right;
            padding-right: 1rem;
            @include Raleway-Bold();
            font-size: 1.8rem;
            line-height: 2.5rem;     
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: $gray3;
        }

        .author__city {
            text-align: right;
            padding-right: 1rem;
            @include Raleway-Regular();
            font-size: 1.4rem;
            line-height: 2rem;     
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: $gray3;
        }
    }
}