.reception {
    padding: 2.35rem 0 4.5rem 0;

    @media (min-width: 1320px) {
        padding: 4.7rem 0 9rem 0;
    }

    .reception__inner {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        align-items: center;

        @media (min-width: 1320px) {
            flex-direction: row;
            justify-content: space-between;
            align-items: initial;
        }

        .reception__left {
            width: 100%;
            max-width: 59.5rem;
            margin-bottom: 3rem;

            @media (min-width: 1320px) {
                margin-bottom: 0;
            }

            .reception__list {

                .reception__item {
                    position: relative;
                    width: 100%;
                    padding: 1.5rem 2.3rem 1.5rem 4.35rem;
                    @include OpenSans-Regular();
                    font-size: 1.6rem;
                    line-height: 2.3rem;
                    color: $gray2;
                    border-radius: .5rem;
                    background: $gray6;

                    @media (min-width: 1320px) {
                        padding: 2.25rem 3.3rem 2.25rem 8.7rem;
                        font-size: 1.8rem;
                        line-height: 2.5rem;
                    }

                    &::before,
                    &::after {
                        position: absolute;
                        content: "";
                    }

                    &::after {
                        top: calc(50% - 1.3rem);
                        left: 1rem;
                        width: 2.6rem;
                        height: 2.6rem;
                        border-radius: 50%;
                        background: $green;

                        @media (min-width: 1320px) {
                            left: 2.4rem;
                            width: 3.6rem;
                            height: 3.6rem;
                        }
                    }
                
                    &::before {
                        z-index: 1;
                        top: calc(50% - .6rem);
                        left: 1.9rem;
                        transform: rotate(45deg);
                        width: .8rem;
                        height: .9rem;
                        border: solid #fff;
                        border-width: 0 .2rem .2rem 0;

                        @media (min-width: 1320px) {
                            top: calc(50% - .2rem);
                            left: 3.7rem;
                            transform: rotate(45deg);
                            width: 1rem;
                            height: 1.1rem;
                        }
                    }

                    &:not(:last-of-type) {
                        margin-bottom: 2rem;
                    }

                    &:last-of-type {
                        margin-bottom: 2.5rem;
                    }
                }
            }

            .reception__warning {
                position: relative;
                padding: 1.5rem 2.3rem 1.5rem 4.35rem;
                @include OpenSans-Regular();
                font-size: 1.6rem;
                line-height: 2.3rem;
                color: $gray6;
                background: $green;

                @media (min-width: 1320px) {
                    padding: 2.7rem 3.3rem 2.7rem 8.7rem;
                    font-size: 1.8rem;
                    line-height: 2.5rem;
                }

                &::before {
                    position: absolute;
                    content: "!";
                    top: calc(50% - 1.4rem);
                    left: 1rem;
                    width: 2.6rem;
                    height: 2.6rem;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    @include OpenSans-Bold();
                    font-size: 1.6rem;
                    line-height: 2.3rem;
                    color: $gray2;
                    border-radius: 50%;
                    background: #fff;

                    @media (min-width: 1320px) {
                        top: calc(50% - 1.8rem);
                        left: 2.4rem;
                        width: 3.6rem;
                        height: 3.6rem;
                        font-size: 1.8rem;
                        line-height: 2.5rem;
                    }
                }
            }
        }

        .reception__right {
            width: 100%;
            max-width: 59.5rem;

            @media (min-width: 1320px) {
                max-width: 55.1rem;
            }

            .home__help {
                margin-bottom: 7rem;

                .home__help-content {

                    p:not(:last-of-type) {
                        margin-bottom: 1rem;
                    }

                    p:last-of-type {
                        margin-bottom: 2.5rem;
                    }
                }

                .btn--blue-call {
                    min-width: 21.1rem;
                }
            }

            .advantages {
                margin: 0;
                padding: 0;
            }
        }
    }

    .numbers-block {
        width: 100%;
        margin-bottom: 1.85rem !important;

        @media (min-width: 768px) {
            margin-bottom: 3.7rem !important;
        }

        &:last-of-type,
        &:nth-last-of-type(2) {

            @media (min-width: 768px) {
                margin-bottom: 0 !important;
            }
        }

        @media (min-width: 768px) {
            width: 50%;
        }
    }

    .section__title--home__help {
        margin-bottom: 2rem;
        color: #333;

        @media (min-width: 768px) {
            margin-bottom: 1.5rem;
            font-size: 2.4rem;
            line-height: 3.3rem;
        }
    }
}