.banner-page {
    &__block {
        position: relative;
        width: 100%;
        max-width: 121rem;
        min-height: 25.9rem;
        border-radius: 0.5rem;
        text-align: center;
        padding: 2rem;
        @media (min-width: 0) {
            background: url(../img/banner-page-s.jpg) 100% 100% / cover no-repeat;

            @media (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 192dpi) {
                background: url(../img/banner-page-s@2x.jpg) 100% 100% / cover no-repeat;
            }
        }

        @media (min-width: 768px) {
            background: url(../img/banner-page-m.jpg) 100% 100% / cover no-repeat;

            @media (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 192dpi) {
                background: url(../img/banner-page-m@2x.jpg) 100% 100% / cover no-repeat;
            }
        }

        @media (min-width: 1320px) {
            padding: 6.6rem 11rem;
            // min-height: initial;
            background: url(../img/banner-page-l.jpg) 100% 100% / cover no-repeat;

            @media (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 192dpi) {
                background: url(../img/banner-page-l@2x.jpg) 100% 100% / cover no-repeat;
            }
        }
    }
}
.banner-page__block-center {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h1 {
        margin: 0;
    }
}
