.content-block {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    @media screen and (min-width: 1320px) {
        flex-direction: row;
    }

    &__img,
    &__text {
        width: 100%;

        @media screen and (min-width: 1320px) {
            width: 50%;
        }
    }

    &__img {
        width: 100%;
        max-width: 35.7rem;
        height: 15.6rem;
        margin-bottom: 2.9rem;
        border-radius: 1rem;

        @media screen and (min-width: 768px) {
            max-width: 66.5rem;
            height: 29.1rem;
            margin-bottom: 3.4rem;
        }

        @media screen and (min-width: 1320px) {
            max-width: 57.9rem;
            height: 25.3rem;
            margin: 0 3rem 0 0;
        }

        img {
            height: 100%;
            border-radius: 1rem;
        }
    }

    &__text {

        @media (min-width: 1320px) {
            max-width: 55.1rem;
        }

        p:not(:last-child) {
            margin-bottom: 1.9rem;

            @media screen and (min-width: 1320px) {
                margin-bottom: 1.5rem;
            }
        }
    }
}

.content-block__img--recommendation {
        order: -1;
        width: 100%;
        width: 23.1rem;
        height: 19.8rem;
        margin-bottom: 2.9rem;
        border-radius: 1rem;

        @media screen and (min-width: 414px) {
            width: 32.3rem;
            height: 27.7rem;
        }

        @media screen and (min-width: 768px) {
            width: 41.6rem;
            height: 30rem;
            margin-bottom: 3.4rem;
        }

        @media screen and (min-width: 1320px) {
            order: 0;
            width: 46.2rem;
            height: 39.6rem;
            margin: 0;
        }

        img {
            border-radius: 1rem;
        }
}