//    H1
.main__title {
    @include OpenSans-Bold();
    font-size: 2.4rem;
    line-height: 3.3rem;
    color: $gray2;

    @media (min-width: 768px) {
        font-size: 3.6rem;
        line-height: 4.9rem;
    }
}

//   H2
.section__title {
    @include OpenSans-Bold();
    font-size: 2rem;
    line-height: 3.3rem;
    color: $gray2;

    @media (min-width: 414px) {
        font-size: 2.4rem;
        line-height: 3.7rem;
    }

    @media (min-width: 1320px) {
        font-size: 3.6rem;
        line-height: 5.6rem;
    }
}

.section__title--light {
    @include OpenSans-Regular();
}

//   ()
.section__subtitle {
    @include OpenSans-Bold();
    font-size: 1.6rem;
    line-height: 2.8rem;
    color: $gray2;

    @media (min-width: 768px) {
        font-size: 1.8rem;
    }

    @media (min-width: 768px) {
        font-size: 2.4rem;
        line-height: 3.7rem;
    }
}

//   ()
// 
.section__description,
.list {
    @include Raleway-Regular();
    font-size: 1.4rem;
    line-height: 2.3rem;
    color: $gray2;

    @media (min-width: 768px) {
        font-size: 1.8rem;
        line-height: 2.8rem;
    }
}

.section__description {
    text-align: center;
}

//  
.text {
    @include OpenSans-Regular();
    font-size: 1.4rem;
    line-height: 2.2rem;
    color: $gray2;
}

.text-italic {
    @include Raleway-Italic();
    font-size: 1.4rem;
    line-height: 2.2rem;
    font-feature-settings: "pnum" on, "lnum" on;
    color: $gray3;
}

//  
.card__title {
    @include OpenSans-Bold();
    font-size: 2.4rem;
    line-height: 3.4rem;
    color: $gray2;
    font-feature-settings: "pnum" on, "lnum" on;
}

//     ()  
.counts__text {
    @include Raleway-Bold();
    font-size: 1.4rem;
    line-height: 2.3rem;
    color: $gray2;

    @media (min-width: 414px) {
        font-size: 1.8rem;
    }
}

//   
.accordion__title {
    @include Raleway-Bold();
    font-size: 1.4rem;
    line-height: 2rem;
    font-feature-settings: "pnum" on, "lnum" on;
    color: $white;

    @media (min-width: 1320px) {
        font-size: 1.8rem;
        line-height: 2.5rem;
    }
}

//    
.policy {
    @include OpenSans-Light();
    font-size: 1.2rem;
    line-height: 1.9rem;
    color: $gray4;

    @media (min-width: 1320px) {
        line-height: 2.2rem;
    }
}

//   
.placeholder {
    @include OpenSans-Regular();
    font-size: 1.4rem;
    line-height: 2.3rem;
    color: $gray5;

    @media (min-width: 1320px) {
        font-size: 2.2rem;
        line-height: 1.4rem;
    }
}

.label {
    @include OpenSans-Bold();
    font-size: 1.8rem;
    line-height: 2.5rem;
    font-feature-settings: 'pnum' on, 'lnum' on;
    color: $gray6;
}

i {
    color: $green;
    font-style: normal;
}

strong {
    @include OpenSans-Bold();
    font-size: 1.8rem;
    line-height: 2.4rem;
    color: $gray2;
}

.reception__warning {
    strong {
        color: $gray6;
    }
}

.method-block {
    strong {
        @include Raleway-Bold();
        font-size: 1.4rem;
        line-height: 2.2rem;
    }
}